.rating{padding:0 5px;border-radius:5px;background-color:rgba(255,255,255,.5);position:absolute;top:3%;left:50%;transform:translateX(-50%);box-sizing:border-box;display:flex;align-items:flex-end;font-size:30px;line-height:.75;cursor:default}.rating .ratingBody{position:relative}.rating .ratingBody::before{color:rgba(0,0,0,.6);content:"☆☆☆☆☆";display:block}.rating .ratingBody .ratingActive{overflow:hidden;position:absolute;width:0;height:100%;top:0;left:0;z-index:-100}.rating .ratingBody .ratingActive::before{color:gold;content:"★★★★★";display:block;position:absolute;width:100%;height:100%;top:0;left:0}.rating .ratingBody .ratingItems{display:flex;position:absolute;width:100%;height:100%;top:0;left:0}.rating .ratingBody .ratingItems .ratingRadio{margin:0;cursor:pointer;flex:0 0 20%;height:100%;opacity:0}.rating .ratingValue{font-weight:bold;text-shadow:2px 2px #000;font-size:80%;line-height:1;padding:0 0 0 10px}